.navigationBar2{
    border-bottom: 1px solid #c9cdd2;
}
.navLinks {
    display: flex;
    gap: 1.5rem;
    margin: 15px 30px;
    
  }
  
.navLinks >li> a {
    display: inline-block;
    padding: 7px 10px 7px 10px;
    text-decoration: none;
    color: #1e2224;
    border-radius: 5px;
    font-size: 14px;
    font-weight: 600;
    font-family: "Work Sans", sans-serif;
  }
  
.navLinks >li> a:hover {
    background-color: #e2e9f2;
    color: #2b74da;
  }
  
.active{
    background-color: #e2e9f2;
    color: #2b74da !important;
}
.timeButton{
    padding: 1.5rem 0;
    display: flex;
    justify-content: right;
}
.timeBtn{
    border: 1px solid #dcdcdc;
    background: #ffffff;
    color: #2b74da;
    font-size: 16px;
    font-weight: 500;
    font-family: "Work Sans", sans-serif;
    text-transform: capitalize;
    padding: 0px 15px;
    border-radius: 5px 0px 0px 5px;
    cursor: pointer;
}
.timeBtn:hover {
    border: 1px solid #2b74da;
    transition: all 0.8s ease-in-out;
    border-radius: 5px 0px 0px 5px;
    background: #2b74da;
    color: #ffffff;
  }
.timeSubmit{
    font-size: 12px;
  font-family: "Work Sans", sans-serif;
  border-radius: 5px;
  border: 1px solid #077838;
  background: #077838;
  color: #ffffff;
  font-weight: 500;
  text-transform: capitalize;
  padding: 5px 10px;
  margin: 0 auto;
  display: block;
}
.timeSubmit:hover {
    border: 1px solid #077838;
    background: #ffffff;
    color: #077838;
    transition: all 0.8s ease-in-out;
  }
  .paginationBtn{
    padding: 1rem 0;
    display: flex;
    justify-content: right;
    gap: 0.5rem;
  }
  .paginationBtn button{
    padding: 0.2rem;
  }
.userProfile{
  /* border: 2px solid red; */
  padding: 1.5rem 3.5rem;
  display: flex;
  justify-content: space-between;
}
.profileImage p{
  color: #1e2224;
    font-size: 16px;
    font-family: "Work Sans";
    text-transform: uppercase;
    font-weight: 600;
    margin-bottom: 15px;
}
.profileImage > img {
  width: 150px;
  height: 150px;
  max-width: 100%;
    object-fit: cover;
    border-radius: 50%;
}
.profileImage > button {
  cursor: pointer;
  color: #1e2224;
  font-size: 16px;
  font-family: "Work Sans", sans-serif;
  text-transform: capitalize;
  font-weight: 600;
  margin-top: 20px;
  background: #e2e9f2;
  padding: 10px;
  border-radius: 5px;
  border: none;
  box-shadow: 0px 4px 10px -2px rgba(0, 0, 0, 0.3);
    
}
.dateTime > p {
  color: #1e2224;
  font-size: 14px;
  font-family: "Work Sans", sans-serif;
  text-transform: capitalize;
  font-weight: 500;
}
.designation{
  padding: 1.5rem 3.5rem;
  display: flex;
  gap: 5rem;
}
.designation>p{
  color: #1e2224;
  font-size: 16px;
  font-family: "Work Sans", sans-serif;
  text-transform: uppercase;
  font-weight: 600;
}
.designation span{
  width: 180px;
  color: #86878a;
  font-size: 14px;
  font-family: "Work Sans", sans-serif;
  font-weight: 500;
}
.personalDetails{
  padding: 1.5rem 3.5rem;
  display: flex;
  justify-content: space-between;
}

.detailsFirstSection{
  width: 25%;
  margin: 1.5rem;
}
.detailsSecondSection{
width: 25%;
margin: 1.5rem;
} 
.profileImage > p, .personalRole > p, .detailsFirstSection > p, .detailsSecondSection > p {
  color: #1e2224;
  font-size: 16px;
  font-family: "Work Sans";
  text-transform: uppercase;
  font-weight: 600;
  margin-bottom: 15px;
}
.detailsFirstSection > ul > li, .detailsSecondSection > ul > li {
   display: flex;
  align-items: center;
  justify-content: space-between;
  gap:1rem;
  margin-bottom: 27px;
  /* text-transform: capitalize; */
}
.detailsFirstSection > ul > li > p, .detailsSecondSection > ul > li > p {
  /* width: 180px; */
  color: #86878a;
  font-size: 14px;
  font-family: "Work Sans";
  font-weight: 500;
  padding: 1rem 0;
}
.detailsFirstSection > ul > li > span, .detailsSecondSection > ul > li > span {
  /* width: 120px; */
  color: #1e2224;
  font-size: 14px;
  font-family: "Work Sans";
  font-weight: 500;
  }

